import React from "react";
import './Feedback.scss'
import { Icon } from "../../../icon/icon.jsx";

const Feedback = ({activeFeedback, setActiveFeedback}) => {

    return (
        <div className={activeFeedback ? 'feedback open' : 'feedback'} onClick={() => setActiveFeedback(!activeFeedback)}>
            <div className="feedback__overlay">
                <div className="feedback__inner" onClick={e => e.stopPropagation()}>
                    <div className='feedback__ic' onClick={() => setActiveFeedback(!activeFeedback)}><Icon className='ic' name='close' width='40' /></div>
                    <h2 className="feedback__title"> </h2>
                    <form action="" className="feedback__form form">
                            <input type="name" className="form__field" placeholder=''/>
                            <input type="number" className="form__field" placeholder=''/>
                            <input type="email" className="form__field" placeholder=' '/>
                            <textarea className="form__field message" placeholder=''/>
                        <div className="form__description">
                             ,    
                            <a href='' className='form__link'>    </a>    
                            <a href='' className="form__link">  </a></div>
                        <button className="form__btn btn"><Icon name='arrowRight' widht='24' color='white'/></button>
                    </form>
                </div>
            </div>
        </div>
    )
}

export default Feedback;